.woocommerce {
    .wc-booster-product-review-inner {
        .star-rating {
            float: right;
            overflow: hidden;
            position: relative;
            height: 1em;
            line-height: 1;
            font-size: 1em;
            width: 5.4em;
            font-family: star;

            &::before {
                content: "sssss";
                color: #cfc8d8;
                float: left;
                top: 0;
                left: 0;
                position: absolute;
            }

            span {
                overflow: hidden;
                float: left;
                top: 0;
                left: 0;
                position: absolute;
                padding-top: 1.5em;

                &::before {
                    content: "SSSSS";
                    top: 0;
                    position: absolute;
                    left: 0;
                }
            }
        }
    }
}
